body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
}

.component-container {
  display: flex;
  background-image: linear-gradient(to right, #ff690f, #eb320d);
  padding: 20px;
}

.input-container {
  display: flex;
  width: 100%;
  font-size: 12px;
}

.input-container__input {
  padding-left: 50px;
  border: none;
  border-radius: 0.1rem;
  width: 100%;
  height: 44px;
  background-color: rgba(214, 212, 212, 0.315);
  font-weight: bolder;
  color: white;
  display: block;
}

.input-clickable {
  margin-top: 13px;
  font-size: 0.9rem;
}

button {
  display: flex;
  border-style: none;
  width: 44px;
  height: 44px;
  background-color: white;
  border-radius: 0.12rem;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  outline: none;
}

button:hover {
  cursor: pointer;
}
.movie-icon {
  position: absolute;
  width: 27px;
  height: 27px;
  left: 30px;
  top: 30px;
}

.fade {
  animation: fade-in-keyframes 0.3s;
}

@keyframes fade-in-keyframes {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.input-clicked {
  border: none;
  border-bottom: 0.3px rgba(214, 212, 212, 0.515) solid;
  border-radius: 0.1rem;
  background-color: white;
  color: black;
  font-weight: bold;
  font-size: 0.9rem;
  width: 100%;
  padding-right: 1px;
  padding-left: 53px;
  padding-bottom: 5px;
  bottom: 40px;
  height: 46px;
  z-index: 2;
  position: relative;
}

.movie-icon-input {
  position: relative;
  bottom: 76px;
  left: 15px;
  z-index: 3;
}

.input-label {
  font-size: 0.7rem;
  position: relative;
  bottom: 70px;
  left: 30px;
  z-index: 3;
  color: grey;
  font-weight: 600;
}

.suggestion-list {
  list-style: none;
  border-radius: 0.1rem;
  background-color: white;
  color: black;
  width: 100%;
  padding-right: 1px;
  padding-left: 51px;
  padding-bottom: 5px;
  bottom: 78px;
  position: relative;
  border: 1px solid rgba(214, 212, 212, 0.515);
  box-shadow: 0px 3px 5px rgba(12, 11, 11, 0.267);
}

.suggestion-list__item {
  padding: 0.8rem 0.1rem;
}

.suggestion-list__item:hover {
  background-color: rgba(214, 212, 212, 0.2);
  cursor: pointer;
}

.title {
  font-size: 0.9rem;
}

.title--suggestions {
  color: rgb(80, 77, 77);
}

.subtitle {
  font-size: 0.7rem;
  font-weight: 400;
}
